body {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

canvas {
    width: 100%;
    height: 100%;
}

#text {
    position: absolute;
    font-family: consolas, sans-serif;
    font-size: 20px;
    top: 65vh;
    width: 100%;
    text-align: center;
    color: lightblue;
    transform: scale(1.5);
}

@keyframes inhale {
    0% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1.5);
    }
}

@keyframes exhale {
    0% {
        transform: scale(1.5);
    }
    100% {
        transform: scale(0.5);
    }
}